body {
  
  background-image: linear-gradient(to right, #e4afcb 0%, #b8cbb8 0%, #b8cbb8 0%, #e2c58b 30%, #c2ce9c 64%, #7edbdc 100%);
}
.App {
  background-image: linear-gradient(45deg, #8baaaa 0%, #ae8b9c 100%);
  min-height: 80vh;
  width: 80vh;
  margin: 40px auto;
  
}

form{
     display: flex;
      justify-content: center; 
}
.todo-i-name{
  border-radius: 10px;
  color: #727277;
  margin-right: 0% !important;
  border: 0px;
  background-color:  #a8caba; 
  margin:20px;
  height: 50px;
  width: 200px;
  padding: 0 20px;
  
}
form input:focus, button:focus{
  outline: none;
}
.todo-i-name::placeholder{
  color:  #09203f 0%;
}
.todo-button{
    border-radius: 10px;
    border:0px;
    margin-left: 0% !important;
    height: 50px;
    width: 80px;
    font-weight: bolder;
    cursor: pointer;
    margin: 20px;
    color: #ffff;
    background-color:  rgb(122,190,160);
}