.list{
    color:#1c2557;
    background-image: linear-gradient(to top, #ff9a9e 0%, #fecfef 99%, #fecfef 100%);
    margin: 20px auto;
    width: 300px;
    height: 50px;
    border-radius: 10px
}
.list p{
    padding: 10px;
    position: relative;
}

.list p span{
    position: absolute;
    right: 10px;
    cursor: pointer;
}

.faicon{
    margin : 0 5px;
    font-size: 15px;
}
.list p input{
    background: transparent;
        border: 0;
        color: #1c2557 ;
        font-size: 18px;
}
.list p input:focus{
    outline: none;
}